.login-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.login-modal {
  background: #1a1a1a;
  padding: 2.5rem;
  border-radius: 0.75rem;
  width: 100%;
  max-width: 400px;
  position: relative;
  border: 1px solid #333;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.4);
  color: white;
}

h2 {
  margin: 0;
  color: #ffbe0b;
  font-size: 1.75rem;
  text-align: center;
}

.login-subtitle {
  color: #ccc;
  text-align: center;
  margin: 1rem 0 2rem;
  font-size: 0.9rem;
}

.error-message {
  background: rgba(231, 76, 60, 0.1);
  color: #e74c3c;
  padding: 0.75rem;
  border-radius: 0.25rem;
  margin-bottom: 1.5rem;
  font-size: 0.875rem;
  border: 1px solid rgba(231, 76, 60, 0.2);
}

.form-group {
  margin-bottom: 1.5rem;
}

label {
  display: block;
  margin-bottom: 0.5rem;
  color: #fff;
  font-weight: 500;
  font-size: 0.9rem;
}

input {
  width: 100%;
  padding: 0.75rem;
  background: #2a2a2a;
  border: 1px solid #333;
  border-radius: 0.25rem;
  font-size: 1rem;
  color: white;
  transition: all 0.2s;
}

input::placeholder {
  color: #666;
}

input:focus {
  outline: none;
  border-color: #ffbe0b;
  box-shadow: 0 0 0 2px rgba(255, 190, 11, 0.2);
}

.login-submit {
  width: 100%;
  padding: 0.875rem;
  background: #ffbe0b;
  color: #1a1a1a;
  border: none;
  border-radius: 0.25rem;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  margin-top: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: all 0.2s;
}

.login-submit:hover {
  background: #ffd60a;
  transform: translateY(-1px);
}

.login-submit:active {
  transform: translateY(0);
}